import { StatusCode } from '../types/StatusCode';
import { ShowErrorProps } from './ShowError';

export type ShowErrorMapFunction = (error: Error | null) => ShowErrorProps;

export type StatusCodeMap = Partial<Record<StatusCode, ShowErrorMapFunction>>;

export type UntypedApplicationCodeMap = Partial<
  Record<string, ShowErrorMapFunction>
>;

export interface ErrorCodesMap
  extends StatusCodeMap,
    UntypedApplicationCodeMap {
  /**
   * When the error is a server error (5xx)
   */
  serverError?: ShowErrorMapFunction;
}
